@charset "UTF-8";
body {
  font-family: "Montserrat";
}

.message-container {
  width: 90%;
  border-radius: 5px;
  border: 2px solid rgba(255, 255, 255, 0);
  font-size: 15px;
  font-weight: bold;
  padding: 1rem;
  margin: 1rem auto;
}
.message-container.success-message {
  background-color: rgba(57, 135, 54, 0.2117647059);
  color: green;
  border-color: green;
}
.message-container.success-message::before {
  content: "üëç";
  margin-right: 15px;
}
.message-container.error-message {
  background-color: rgba(135, 54, 54, 0.2117647059);
  color: red;
  border-color: red;
}
.message-container.error-message::before {
  content: "‚ùå";
  margin-right: 15px;
}
.message-container.warning-message {
  background-color: rgba(223, 248, 0, 0.2117647059);
  color: #d4c926;
  border-color: #d4c926;
}
.message-container.warning-message::before {
  content: "‚ö†Ô∏è";
  margin-right: 15px;
}

h1 {
  text-align: center;
  color: green;
  font-size: 2.5rem;
  padding: 1rem 2rem 2rem;
  border-bottom: 2px solid green;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 5px 20px;
}

.note-content {
  color: #333333;
  padding-top: 3px;
  font-size: 15px;
}

.important-tag {
  margin-left: 40px;
  padding: 0.5rem;
  background-color: #eee;
  color: #bbb;
  font-size: 12px;
  font-weight: bold;
  border-radius: 5px;
}

.important-button {
  text-transform: uppercase;
  min-width: 150px;
  border-radius: 5px;
  font-size: 12px;
  font-weight: bold;
  background-color: #eee;
  color: #bbb;
}
.important-button.enabled {
  background-color: #333333;
  color: white;
}